//burger
//--------------------------------------------------------------------
const burger = document.querySelector('.burger');

burger.addEventListener('click', (e) => {
  e.currentTarget.classList.toggle('burger--active');
});
//SLIDER
//--------------------------------------------------------------------
const Width           = document.documentElement.clientWidth;
if (Width <= 992) {
  const btnNext       = document.querySelector('.slider__arrow--right');
  const btnPrev       = document.querySelector('.slider__arrow--left');

  const Slider        = document.querySelector('.slider');
  const SliderContent = document.querySelector('.intro__list');
  const SliderElem    = SliderContent.childElementCount;


  // Нужно менять
  const ActiveElem = 1;
  const FlexGap = 10;


  let Sdvig = (Width - FlexGap * (SliderElem - 1)) / ActiveElem;
  let NumSlide = 0;

  btnNext.onclick = function () {
    if (NumSlide + ActiveElem - 1 < SliderElem - 1) {
      ++NumSlide;
      SliderContent.setAttribute('style', `right: ${Math.round(Sdvig * NumSlide) + FlexGap * (NumSlide )}px`);

    }
  }
  btnPrev.onclick = function () {
    if (NumSlide - 1 >= 0) {
      --NumSlide;
      SliderContent.setAttribute('style', `right: ${Math.round(Sdvig * NumSlide) + FlexGap * (NumSlide)}px`);

    }
  }
}

//MAP
//--------------------------------------------------------------------
let quantityPoints = 17;
for (let i = 1; i <= 17; ++i) {
  let elem      = document.querySelector('#point' + i);
  let partMap   = document.querySelector('#map' + i);
  elem.addEventListener('mouseenter', (event) => {
    partMap.classList.add('our-objects__items--active');
  });
  elem.addEventListener('mouseleave', (event) => {
    partMap.classList.remove('our-objects__items--active');
  });
}

//slider
//--------------------------------------------------------------------
const images = ['../img/slide1.png',
  '../img/slide1.png',
  '../img/slide1.png']
const leftBtn   = document.querySelector('.about-us__arrow--left');
const rightBtn  = document.querySelector('.about-us__arrow--right');
const selector  = document.querySelector('.about-us');
let activeSlide = 0;

leftBtn.addEventListener('click', (event) => {
  if (activeSlide - 1 == -1) {
    activeSlide = images.length
  }
  activeSlide -= 1
  selector.style.background = `url(${images[activeSlide]})`;
  selector.style.backgroundRepeat = `no-repeat`;
  selector.style.backgroundSize = `cover`;
  selector.style.backgroundPosition = `0 0`;
})
rightBtn.addEventListener('click', (event) => {
  if (activeSlide + 1 == images.length) {
    activeSlide = -1
  }
  activeSlide += 1
  selector.style.background = `url(${images[activeSlide]})`;
  selector.style.backgroundRepeat = `no-repeat`;
  selector.style.backgroundSize = `cover`;
  selector.style.backgroundPosition = `0 0`;
})
//FOOTER
//--------------------------------------------------------------------
const items = document.querySelectorAll('.footer__top-block');

items.forEach(item => {
  item.addEventListener('click', (event) => {
    item.parentNode.classList.toggle('footer__top-items--active')
  })  
});